            />
          </div>
          <div>
            <label for="phone">보호자의 연락처</label>
            <input
              type="number"
              id="
birth-date"
            />
          </div>
          <button>등록(등록시 보호자에게 연락이 갑니다)</button>
        </form>
      </div>
      <div id="alarm">
        <h3>약 복용 및 물 섭취 알람</h3>
        <span>시간</span>
        <label class="switch">
          <input type="checkbox" />
          <span class="slider round"></span>
        </label>
        <h3>약 복용 및 물 섭취 알람</h3>
        <span>시간</span>
        <label class="switch">
          <input type="checkbox" />
          <span class="slider round"></span>
        </label>
        <h3>약 복용 및 물 섭취 알람</h3>
        <span>시간</span>
        <label class="switch">
          <input type="checkbox" />
          <span class="slider round"></span>
        </label>
      </div>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style>
#setting-p {
  display: flex;
  flex-direction: column;
  background-color: #ffca7c;
  height: 90vh; /* 높이 변경 */
  padding: 0 20px;
  overflow: hidden; /* 컨테이너 내부의 오버플로 숨김 */
}

#setting-p-container {
  position: relative;
  height: 100%; /* 부모의 100%를 차지하도록 설정 */

  background-color: #ffca7c;
  overflow: auto;
}

#setting-p-container #admin {
  border-radius: 10px;
  background-color: white;
  height: 330px;
}
#setting-p-container #alarm {
  border-radius: 10px;
  margin-top: 20px;
  background-color: white;
  height: 330px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>
