import { createWebHistory, createRouter } from "vue-router";
import MainP from "./components/MainP";
import CheckP from "./components/CheckP";
import RegisterP from "./components/RegisterP";
import ProfileP from "./components/ProfileP";
import SettingP from "./components/SettingP";
import EditProfile from './components/EditProfile';
import FoodP from './components/FoodP';
import FoodIngredientsP from './components/FoodIngredientsP';
import AlarmP from './components/AlarmP';
import AdminP from './components/AdminP';

const routes = [
  {
    path: "/",
    component: CheckP,
  },
  {
    path: "/register",
    component: RegisterP,
  },
  {
    path: "/main",
    component: MainP,
  },
  {
    path: "/profile",
    component: ProfileP,
  },
  {
    path: "/setting",
    component: SettingP,
  },
  {
    path: '/edit',
    name: 'EditProfile',
    component: EditProfile,
    props: true  // 이 설정을 통해 params를 props로 받을 수 있습니다.
  },
  {
    path: "/food",
    component: FoodP,
  },
  {
    path: "/foodIngredients",
    component: FoodIngredientsP,
  },
  {
    path: "/alarm",
    component: AlarmP,
  },
  {
    path: "/admin",
    component: AdminP,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
