<template>
    <div id="edit-profile-p">
      <h1>사용자 정보 수정</h1>
      <form @submit.prevent="submitForm">
        <label for="name">이름:</label>
        <input type="text" id="name" v-model="userProfile.name" required><br><br>
  
        <label for="address">주소:</label>
        <input type="text" id="address" v-model="userProfile.address" required><br><br>
  
        <label for="age">나이:</label>
        <input type="number" id="age" v-model="userProfile.age" required><br><br>
  
        <label for="weight">몸무게 (kg):</label>
        <input type="number" id="weight" v-model="userProfile.kg" required><br><br>
  
        <label for="height">키 (cm):</label>
        <input type="number" id="height" v-model="userProfile.cm" required><br><br>
  
        <label for="familyHistory">가족력:</label>
        <textarea id="familyHistory" v-model="userProfile.familyHistory" required></textarea><br><br>
  
        <label for="currentDisease">현재 가지고 있는 병:</label>
        <textarea id="currentDisease" v-model="userProfile.currentDisease" required></textarea><br><br>
  
        <button type="submit">저장</button>
      </form>
      <router-link to="/profile">취소</router-link>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        userProfile: {}
      };
    },
    created() {
      this.userProfile = this.$route.params;
    },
    methods: {
      submitForm() {
        // 여기에서 폼 제출 로직을 추가합니다.
        // 예를 들어 API 호출을 통해 서버에 데이터를 전송할 수 있습니다.
        console.log('Updated Profile:', this.userProfile);
        // 저장 후, 사용자 정보 페이지로 돌아가기
        this.$router.push('/');
      }
    }
  }
  </script>
  
  <style>
  #edit-profile-p {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }
  
  #edit-profile-p input, #edit-profile-p textarea {
      width: 100%; /* 입력 필드 너비 조정 */
      padding: 8px; /* 편안한 타이핑을 위한 패딩 */  
  }
  </style>
  