<template>
    <div id="profile-p">
      <div id="user-img"></div>
      <p>이름: {{ userProfile.name }}</p>
      <p>주소: {{ userProfile.address }}</p>
      <p>나이: {{ userProfile.age }}</p>
      <p>몸무게: {{ userProfile.kg }}kg</p>
      <p>키: {{ userProfile.cm }}cm</p>
      <p>가족력: {{ userProfile.familyHistory }}</p>
      <p>현재 가지고 있는 병: {{ userProfile.currentDisease }}</p>
      <button @click="editProfile">수정</button>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        userProfile: {
          name: "홍길동",
          age: 30,
          address: "주소주소",
          kg: 60,
           cm: 190, 
           familyHistory: "sssss",
           currentDisease: "sssss"
        }
      };
    },
    methods: {
      editProfile() {
        this.$router.push({ name: 'EditProfile', params: this.userProfile });
      }
    }
  }
  </script>
  
  <style>
  #profile-p {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }
  
  #profile-p #user-img {
      border-radius: 50%;
      width: 100px;
      height: 100px;
      background-color: rgb(172, 189, 184);
  }
  </style>
  