<template>
  <div id="food-p">
    <h1 id="title">식자재관리</h1>
    <div id="food-p-container">
      <div
        class="message"
        v-for="(message, index) in messages"
        :key="index"
        :class="{
          'user-message': message.user,
          'non-user-message': !message.user,
        }"
      >
        <div class="message-content">{{ message.text }}</div>
      </div>

      <div class="message-input">
        <input type="text" v-model="newMessage" @keyup.enter="sendMessage" />
      </div>
    </div>
    <div id="btn">
      <button>
        <img
          width="50px"
          src="https://previews.123rf.com/images/arcady31/arcady311108/arcady31110800017/10101139-%EC%83%98%ED%94%8C-%EC%9A%B0%ED%91%9C.jpg"
          alt=""
        />
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      messages: [
        { text: "콩나물이랑 돼지고기랑 사왔어", user: true },
        {
          text: "콩나물, 돼지고기가 맞나요?",
          user: false,
        },
        { text: "응 맞아", user: true },
        {
          text: "네 알겠습니다.",
          user: false,
        },
      ],
      newMessage: "",
      lastUser: true, // 마지막 메시지의 유저 상태를 추적
    };
  },
  methods: {
    sendMessage() {
      if (this.newMessage.trim()) {
        this.messages.push({ text: this.newMessage, user: true });
        this.newMessage = "";
      }
    },
  },
};
</script>

<style scoped>
#food-p {
  display: flex;
  flex-direction: column;
  background-color: #ffca7c;
  height: 90vh; /* 높이 변경 */
  padding: 0 20px;
  overflow: hidden; /* 컨테이너 내부의 오버플로 숨김 */
}

#food-p-container {
  position: relative;
  height: 100%; /* 부모의 100%를 차지하도록 설정 */
  border-radius: 10px;
  background-color: white;
  padding: 10px;
  overflow: auto;
}

.message {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* 모든 메시지를 왼쪽 정렬 */
  margin-bottom: 10px;
}

.user-message .message-content,
.non-user-message .message-content {
  color: black;
  padding: 10px;
  border-radius: 10px;
  flex-grow: 1; /* 메시지 내용이 가능한 공간을 채우도록 설정 */
  margin: 0;
  background-color: #d3d3d3; /* 통일된 배경색으로 설정 */
  text-align: left; /* 텍스트를 왼쪽으로 정렬 */
}

.user-message .message-content {
  background-color: white; /* 사용자 메시지의 배경색 변경 */
}

.message-input {
  position: absolute; /* 절대적 위치 설정 */
  bottom: 0; /* 하단에 고정 */
  left: 0; /* 왼쪽에 맞춤 */
  width: 90%; /* 너비를 부모 컨테이너에 맞춤 */
  padding: 10px;
}

.message-input input {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 10px;
  background-color: rgb(215, 215, 215);
}

#food-p #btn {
  display: flex;
  justify-content: center;
  align-content: center;
}

#food-p div button {
  height: 80px;
  width: 60px;
  margin: 0;
  margin-top: 50px; /* 버튼의 상단 여백 추가 */
  padding: 0;
}

</style>
